:local(.Examples) {
    .example > header {
        margin-bottom: 20px;
    }
    .example h3 {
        margin-bottom: 5px;
    }
    .example li + li {
        margin-top: 20px;
    }
    .example code {
        font-size: small;
    }
    .push {
        display: block;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    nav {
        background: #eee;
        margin-bottom: 20px;
    }
    nav ul {
        display: flex;
        padding: 0;
        margin: 0;
        list-style: none;
        justify-content: center;
    }
    nav ul li {
        display: flex;
    }
    nav ul li + li {
        margin-left: 10px;
    }
    nav li a {
        padding: 5px;
    }
}
