@keyframes busy-button {
    0% {
        transform: scale(0.9);
    }
    70% {
        transform: scale(1);
        box-shadow: 0 0 0 5px rgba(24, 180, 253, 0.5);
    }
    100% {
        transform: scale(0.9);
        box-shadow: 0 0 0 0 rgba(24, 180, 253, 0.5);
    }
}

:local(.VanillaExample) {
    button {
        padding: 5px 15px;
        border: 1px solid #ccc;
        appearance: none;
        outline: none;
        &:hover {
            outline: 1px solid #ccc;
        }
        & + button {
            margin-left: 10px;
        }
    }
    button.success {
        background: limegreen;
        color: darkgreen;
    }
    button.error {
        background: red;
        color: white;
    }
    button.busy {
        animation: busy-button 1.5s infinite;
        :hover {
            animation: none;
        }
    }
}
